<template>
  <div class="v-header-wrap">
    <span v-if="!type" class="go-back" @click.stop.prevent="$router.go(-1)"></span>
    <span v-else class="close" @click.stop.prevent="$router.go(-1)"></span>
    <p class="title" v-text="title || metaTitle"></p>
  </div>
</template>

<script>
  export default {
    name: "MyHeader",
    props: {
      type: {
        type: Boolean,
        default: false
      },
      title: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        metaTitle: ''
      }
    },
    created() {
      this.metaTitle = this.$route.meta.title || '';
    },
    watch: {
      '$route'(to) {
        this.metaTitle = to.meta.title || '';
      }
    }
  }
</script>

<style scoped lang="less">
  .v-header-wrap {
    height: 48px;
    background-color: #3DA0FF;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 8px;

    .close {
      width: 25px;
      height: 25px;
      background-size: 12.5px auto;
      background-position: center;
      background-repeat: no-repeat;
      background-image: url('../assets/images/close.png');
    }
    .go-back {
      width: 25px;
      height: 25px;
      background-size: 12.5px auto;
      background-position: center;
      background-repeat: no-repeat;
      background-image: url('../assets/images/arrow_back.png');
    }

    p {
      flex: 1;
      font-size: 18px;
      text-align: center;
      color: #F0EDEE;

      &.pd-r {
        padding-right: 30px;
      }
    }
  }
</style>
